import message from './message'

let vm

export default function install (Vue) {
  Vue.prototype.$laMsg = function (msg) {
    // message should be a singleton
    // use a closure to maintain this object
    if (!vm) {
      const LaMsg = Vue.extend(message)
      vm = new LaMsg().$mount()
      this.$root.$el.appendChild(vm.$el)
    }

    vm.msg = msg
  }
}



