export const partnerRouter = [
  {
    path: '/partner-login',
    name: '合作商登录',
    component: () => import('@/views/login/partner-login')
  },
  {
    path: '/partner-home',
    name: '合作商首页',
    component: () => import('@/views/home/partner-home')
  },
  {
    path: '/partner-account',
    name: '合作商账号资料',
    component: () => import('@/views/account-information/partner-account')
  }
]



