export const serviceRouter = [
  {
    path: '/service-login',
    name: '服务点登录',
    component: () => import('@/views/login/service-login')
  },
  {
    path: '/service-home',
    name: '服务点首页',
    component: () => import('@/views/home/service-home')
  },
  {
    path: '/service-account',
    name: '服务站账号资料',
    component: () => import('@/views/account-information/service-account')
  },
  {
    path: '/service-list',
    name: '服务站列表',
    component: () => import('@/views/service-list/service-list')
  },
  {
    path: '/order-list',
    name: '订单列表',
    component: () => import('@/views/order-list/order-list')
  },
  {
    path: '/recycle-configuration',
    name: '回收配置',
    component: () => import('@/views/recycle-configuration/recycle-configuration')
  }
]



