let timer

export default {
  template: `
    <transition name="slidedown">
      <div v-if="msg" class="la-msg">{{ msg }}</div>
    </transition>
  `,
  data () {
    return {
      msg: ''
    }
  },
  watch: {
    msg () {
      timer = setTimeout(() => {
        this.msg = ''
        clearTimeout(timer)
      }, 2500)
    }
  }
}



