import Vue from 'vue'
import Router from 'vue-router'
// 导入外部路由模块
import {serviceRouter} from './service'
import {partnerRouter} from './partner'

Vue.use(Router)

const router = new Router({
  // 说明： 请将每个模块的页面路由单独放在一个文件然后再引入此处
  routes: [
    {
      path: '/',
      redirect: '/partner-login'
    },
    // 请将外部导入的路由模块写在此处
    ...serviceRouter,
    ...partnerRouter,
    // 路径不存在则导航到登录页
    {
      path: '*',
      redirect: '/partner-login'
    }
  ]
})

// 路由守卫 对路由权限进行控制和判断   暂时关闭
router.beforeEach((to, from, next) => {
  if (!sessionStorage.token && to.path !== '/partner-login' && to.path !== '/service-login') {
    next({path: '/partner-login'})
  } else {
    next()
  }
})
export default router



